.theme-switch__wrap {
  width: 40px;
  height: 20px;
  border-radius: 20px;
  flex-shrink: 0;
  align-items: center;
  padding: 2px 1px;
  display: flex;
  position: relative;
  box-shadow: inset 1px 1px 1px #4b48db, inset -1px -1px 1px #4b48db;
}

.theme-switch__toggle {
  opacity: 0;
  height: 0;
  width: 0;
  pointer-events: none;
  margin: 0;
  padding: 0;
  position: absolute;
  top: 0;
  left: 0;
}

.theme__track {
  width: 100%;
  height: 100%;
  color: rgba(0, 0, 0, 0);
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  border-radius: inherit;
  z-index: 1;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  position: absolute;
  top: 0;
  left: 0;
}

.theme__marker {
  width: 16px;
  height: 16px;
  z-index: 2;
  pointer-events: none;
  background-color: #4b48db;
  border-radius: 50%;
  transition: transform .25s linear;
  position: relative;
}

.theme-switch__toggle:checked + .theme__track {
  background-color: #4b48db;
}

.theme-switch__toggle:checked ~ .theme__marker {
  background-color: #fff;
  transform: translateX(22px);
}

.theme-switch__toggle:not(:checked) ~ .theme__marker {
  background-color: #4b48db;
}

